  export const filterEmoji = (searchText: string, maxReturn = 20) => {

	const filtered_emojis = EmojiList.filter(emoji => {

		if (emoji.title.toLowerCase().includes(searchText.toLowerCase())) {
			return true
		}
		if (emoji.keywords.includes(searchText.toLocaleLowerCase())) {
			return true
		}
		if (emojiUnicode(emoji.symbol) === emojiUnicode(searchText)) {
			return true
		}

		return false
	})

	return filtered_emojis.slice(0, maxReturn)
  }

  export const getEmojis = (maxReturn = 20) => {
	return EmojiList.slice(0, maxReturn)
  }

  export const emojiUnicode = (emoji: any) => {

	let comp
	if (emoji.length === 1) {
		comp = emoji.charCodeAt(0)
	}
	comp = (emoji.charCodeAt(0) - 0xD800) * 0x400 + (emoji.charCodeAt(1) - 0xDC00) + 0x10000

	if (comp < 0) {
		comp = emoji.charCodeAt(0)
	}
	return comp.toString('16')

  } 